import { Component, OnInit, ViewEncapsulation, AfterViewInit, ElementRef, ViewChild, HostBinding} from '@angular/core';
import { Helpers } from '../../../../../../../helpers';
import { ScriptLoaderService } from '../../../../../../../_services/script-loader.service';
import { ROUND_ANTICLOCK_ANIMATION }   from '../../../../../../../animations/round-anticlock.animation';
import { FLY_IN_OUT_ANIMATION }   from '../../../../../../../animations/fly-in-out.animation';
import {HttpClientModule} from '@angular/common/http';
import {HttpClient, HttpHeaders} from '@angular/common/http';
import {FormsModule, ReactiveFormsModule} from '@angular/forms';
import { MatInputModule, MatNativeDateModule, MatSelectModule, MatSort, PageEvent} from '@angular/material';
import { Router, Event, NavigationStart, NavigationEnd, ActivatedRoute } from '@angular/router';
import {CdkTableModule} from '@angular/cdk/table';
import {DataSource} from '@angular/cdk/collections';
import {ListComponent, SafeHtmlPipe} from '../../../../../list/list.component'
import {BehaviorSubject} from 'rxjs/BehaviorSubject';
import {Observable} from 'rxjs/Observable';
import { FormControl, Validators, FormGroup, ValidationErrors } from '@angular/forms';
import 'rxjs/add/operator/startWith';
import 'rxjs/add/observable/merge'; 
import 'rxjs/add/operator/map';
import 'rxjs/add/operator/debounceTime';
import 'rxjs/add/operator/distinctUntilChanged';
import 'rxjs/add/observable/fromEvent';
import 'rxjs/add/operator/filter' ;
import { ToastComponent } from '../../../../../../layouts/toast-notification/toast.component';


@Component({
    selector: ".m-grid__item.m-grid__item--fluid.m-wrapper",
    templateUrl: "./brochure-add.component.html",
    encapsulation: ViewEncapsulation.None,
	animations: [ 
    FLY_IN_OUT_ANIMATION,
		ROUND_ANTICLOCK_ANIMATION
  ],
})

export class BrochureAddComponent    {
    @HostBinding('@flyInOutTrigger') flyInOutTrigger = 'in';
	@ViewChild(ToastComponent) toast: ToastComponent;
	
	 priorityForm: FormGroup;
	 priorityopprId = 0;  
	 status;
	 priorityoppr= new PriorityOpprData(); 
	 bootstrapFormValues = [];
	 msg = [];
 
	 bootstrapMsg='';
	 priorityopprdatemsg:string;

 constructor(private http: HttpClient, private router: Router, private route:ActivatedRoute) {
	this.route.queryParams.filter(params => params.priorityopprId).subscribe(params=>{
			if(params.priorityopprId != undefined){
				this.priorityopprId=params.priorityopprId; 
				}
		}); 
	
	if(this.priorityopprId == 0){
		this.status="Add";
	}else{
		this.status="Update";
		this.populateData();
	}
	}
	
     ngOnInit() {
		this.priorityForm = new FormGroup({			
			'priorityopprId' : new FormControl(''),
			'priorityopprName' : new FormControl('', [ Validators.required]),
			'priorityopprDesc' : new FormControl('', [ Validators.required]),
			'priorityopprEntryId' : new FormControl(''),
			'priorityopprEntryDate' : new FormControl(''),
			'priorityopprModifiedId' : new FormControl(''),
			'priorityopprModifiedDate' : new FormControl(''),
		});
  }
	  
	    get priorityopprName(){ return this.priorityForm.get('priorityopprName')};
	    get priorityopprDesc(){ return this.priorityForm.get('priorityopprDesc')};
	    get priorityopprEntryId(){ return this.priorityForm.get('priorityopprEntryId')};
	    get priorityopprEntryDate(){ return this.priorityForm.get('priorityopprEntryDate')};
	    get priorityopprModifiedId(){ return this.priorityForm.get('priorityopprModifiedId')};
	    get priorityopprModifiedDate(){ return this.priorityForm.get('priorityopprModifiedDate')};
	  
	  populateData(){
		const headers = new HttpHeaders({'Content-Type':'application/json'});
		
		 this.http.post(sessionStorage.getItem('requestUrl')+'/axelacrm/portal/brochure-data',
									JSON.stringify({ priorityopprId :this.priorityopprId,
												   	requestType : "update" }),
						{ headers, withCredentials: true }).subscribe(
			 										data => {
			 													this.priorityoppr = data['populateData'][0];
															},
													(err) => {
																console.log("ERROR: "+err);
															 });			 
		}
	   
	 
	  
	  ngAfterViewInit(){
		    $.getScript('assets/app/js/bootstrap-material-datetimepicker.js',function(){
		   });
	  }
 
 	 toggleAnimation(){
			this.router.navigate(['portal/brochure-list']);
		 }
		   

	onSubmit() {
			this.bootstrapFormValues = $('#m_form_1').serializeArray();
			for(var i=0;i<this.bootstrapFormValues.length;i++){
				this.priorityoppr[this.bootstrapFormValues[i].name]=this.bootstrapFormValues[i].value;
				
				if(this.bootstrapFormValues[i].name=="priorityopprDesc"){
					if(this.bootstrapFormValues[i].value==""){
						this.bootstrapMsg = this.bootstrapMsg +  'Enter Due Date!<br>';
						this.priorityopprdatemsg="<font class='errorMsg'>Enter Due Date</font>";
						
					}else{
						this.priorityopprdatemsg = "";
					}
				}
			}
		if(this.bootstrapMsg != ''){
				this.msg[0]='Error';
			 	this.msg[1]='Enter the Mandatory Fields' ;
				this.toast.showtoast(this.msg);
				this.bootstrapMsg='';
			}
		
		 if(!this.priorityForm.invalid && this.bootstrapMsg == ''){
			  const headers = new HttpHeaders({'Content-Type':'application/json'});
		if(this.priorityopprId == 0){ 
			this.priorityoppr['requestType'] = 'add';
		}else{
			this.priorityoppr['requestType'] = 'update';
			this.priorityoppr['priorityopprId'] = this.priorityopprId+'';
		}
		 this.http.post(sessionStorage.getItem('requestUrl')+'/axelacrm/portal/brochure',
									JSON.stringify(this.priorityoppr),
						{ headers, withCredentials: true }).subscribe(data => {
			 											if(data['successmsg'].includes('Successfully')){
														this.msg[0]='success';
			 											this.msg[1]=data['successmsg'];
														}else{
														this.msg[0]='Error';
			 											this.msg[1]=data['successmsg'];
														}
			 											this.toast.showtoast(this.msg);
			 											this.toggleAnimation();
			 											this.priorityForm.reset();
														},
														err =>{
			 												console.log(err['error']['desc']);
			 												this.msg[0]='Error';
			 												this.msg[1]=err['error'];
			 												this.toast.showtoast(this.msg);
														});
			 console.log(this.priorityoppr);
			
			}
	  }


  onDelete(){
		 this.priorityoppr['requestType'] = 'delete';
		 this.priorityoppr['priorityopprId'] = this.priorityopprId+'';
		 const headers = new HttpHeaders({'Content-Type':'application/json'});
		 this.http.post(sessionStorage.getItem('requestUrl')+'/axelacrm/portal/brochure',
									JSON.stringify(this.priorityoppr),
						{ headers, withCredentials: true }).subscribe(data => {
			 											if(data['successmsg'].includes('Successfully')){
														this.msg[0]='success';
			 											this.msg[1]=data['successmsg'];
														}else{
														this.msg[0]='Error';
			 											this.msg[1]=data['successmsg'];
														}
			 											this.toast.showtoast(this.msg);
			 											this.toggleAnimation();
			 											this.priorityForm.reset();
														},
														err =>{
			 												console.log(err['error']['desc']);
			 												this.msg[0]='Error';
			 												this.msg[1]=err['error'];
			 												this.toast.showtoast(this.msg);
														});
		 
	 }
}

export class PriorityOpprData {
	
	priorityopprId: string ;
	priorityopprName: string;
	priorityopprDesc: string;
	priorityopprDuehrs: string='';
	priorityopprTrigger1Hrs: string='';
	priorityopprTrigger2Hrs: string='';
	priorityopprTrigger3Hrs: string='';
	priorityopprTrigger4Hrs: string='';
	priorityopprTrigger5Hrs: string='';
	priorityopprEntryId:string;
	priorityopprEntryBy:string;
	priorityopprEntryDate:string;
    priorityopprModifiedId:string;
	priorityopprModifiedBy:string;
    priorityopprModifiedDate:string;
}




