import { Component, OnInit, ViewEncapsulation, AfterViewInit, ElementRef, ViewChild, HostBinding } from '@angular/core';
import { Helpers } from '../../../../../../../helpers';
import { ScriptLoaderService } from '../../../../../../../_services/script-loader.service';
import { ROUND_ANTICLOCK_ANIMATION }   from '../../../../../../../animations/round-anticlock.animation';
import { FLY_IN_OUT_ANIMATION }   from '../../../../../../../animations/fly-in-out.animation';
import {HttpClientModule} from '@angular/common/http';
import {HttpClient, HttpHeaders} from '@angular/common/http';
import {FormsModule, ReactiveFormsModule} from '@angular/forms';
import { MatInputModule, MatNativeDateModule, MatSelectModule, MatSort, PageEvent} from '@angular/material';
import { Router, Event, NavigationStart, NavigationEnd, ActivatedRoute } from '@angular/router';
import {CdkTableModule} from '@angular/cdk/table';
import {DataSource} from '@angular/cdk/collections';
import {ListComponent, SafeHtmlPipe} from '../../../../../list/list.component'
import {BehaviorSubject} from 'rxjs/BehaviorSubject';
import {Observable} from 'rxjs/Observable';
import { FormControl, Validators, FormGroup } from '@angular/forms';
import 'rxjs/add/operator/startWith';
import 'rxjs/add/observable/merge'; 
import 'rxjs/add/operator/map';
import 'rxjs/add/operator/debounceTime';
import 'rxjs/add/operator/distinctUntilChanged';
import 'rxjs/add/observable/fromEvent';
import { ToastComponent } from '../../../../../../layouts/toast-notification/toast.component';
import { SmartSearchComponent } from '../../../../../../layouts/smart-search/smart-search.component';

@Component({
    selector: ".m-grid__item.m-grid__item--fluid.m-wrapper",
    templateUrl: "./brochure-list.component.html",
    encapsulation: ViewEncapsulation.None,
	animations: [ 
		FLY_IN_OUT_ANIMATION,
    ROUND_ANTICLOCK_ANIMATION
  ],
})

export class BrochureListComponent implements OnInit,AfterViewInit {
	
    @ViewChild(SmartSearchComponent)smart: SmartSearchComponent;
	@HostBinding('@flyInOutTrigger') flyInOutTrigger = 'in';
	@ViewChild(ListComponent) list: ListComponent;
    @ViewChild(ToastComponent) toast: ToastComponent;
	
	listHide: boolean = true;
	smartHide: boolean = true;
	animateSmart: string = 'out';
	BranchId: string = '0';
	ModelId: string = '0';
	ItemId: string = '0';
	brochureId = 0;  
	brochureListForm: FormGroup;
	branch = [];
	model = [];
	item = [];
	 
   constructor(private _script: ScriptLoaderService, 	private http: HttpClient,private router: Router, private route:ActivatedRoute) {
	this.route.queryParams.filter(params => params.BranchId).subscribe(params=>{
			if(params.BranchId != undefined){
				this.BranchId=params.BranchId; 
				this.list.listUrl=sessionStorage.getItem('requestUrl')+"/axelacrm/sales/brochure-list"; 
				this.list.listParam['BranchId'] = this.BranchId;
				this.list.listParam['ModelId'] = this.ModelId;
				this.list.listParam['ItemId'] = this.ItemId;
				this.list.getData();
				
			}
		}); 
	}

    ngOnInit() {
//	    this.smart.smartUrl = sessionStorage.getItem('requestUrl')+"/axelacrm/sales/brochure-smartsearch";
  		this.list.listUrl=sessionStorage.getItem('requestUrl')+"/axelacrm/sales/brochure-list";
		
		this.brochureListForm = new FormGroup({	
				'brochureBranchId' : new FormControl('',[ Validators.required] ),
				'brochureModelId' : new FormControl('',[ Validators.required] ),
				'brochureItemId' : new FormControl('',[ Validators.required] ),
			});
		
		const headers = new HttpHeaders({'Content-Type':'application/json'});
	   this.http.post<any[]>(sessionStorage.getItem('requestUrl')+'/axelacrm/customer-populate/branch',
								JSON.stringify({ brochureBranchId :'1'}),
							{ headers, withCredentials: true }).subscribe(data =>{
																this.branch = data['populateBranch'];
																});
		this.http.post<any[]>(sessionStorage.getItem('requestUrl')+'/axelacrm/customer-populate/model',
								JSON.stringify({ brochureModelId :'1'}),
							{ headers, withCredentials: true }).subscribe(data =>{
																this.model = data['populateModel'];
																});
		this.http.post<any[]>(sessionStorage.getItem('requestUrl')+'/axelacrm/customer-populate/item',
								JSON.stringify({ brochureItemId :'1'}),
							{ headers, withCredentials: true }).subscribe(data =>{
																this.item = data['populateItem'];
																});
	}
	 
	 get brochureBranchId(){ return this.brochureListForm.get('brochureBranchId')}; 
	 get brochureModelId(){ return this.brochureListForm.get('brochureModelId')}; 
	 get brochureItemId(){ return this.brochureListForm.get('brochureItemId')}; 

   ngAfterViewInit(){
		}

	smartToggle(){
		  if(this.smartHide){
			  this.animateSmart = 'in';
			  this.smartHide = false;
		  }else{
			  this.animateSmart='out';
			  this.smartHide = true;
		  }
	  }
	    
	toggleAnimation(){
		   this.router.navigate(['portal/brochure-list/brochure-add'], {queryParams : { brochureId : this.brochureId}});
	  }
	
	  listClickListener(listParam: any[]){
		if(listParam[0] == 'updateBrochure'){
			this.brochureId = listParam[1];
			this.toggleAnimation();
		}
	}
	 
	  onSelect(value1){
		   this.listHide = false;
		   this.list.listUrl=sessionStorage.getItem('requestUrl')+"/axelacrm/portal/brochure-list"; 
		   this.list.listParam['brochureItemId'] = value1;
		   this.list.getData();
		}
	  
	  populateModel(value){
		  const headers = new HttpHeaders({'Content-Type':'application/json'});
		    this.http.post<any[]>(sessionStorage.getItem('requestUrl')+'/axelacrm/customer-populate/model',
								JSON.stringify({ brochureModelId :'1'}),
							{ headers, withCredentials: true }).subscribe(data =>{
																this.model = data['populateModel'];
																});
		  
	  }
	 
	  populateItem(value){
		  const headers = new HttpHeaders({'Content-Type':'application/json'});
		   this.http.post<any[]>(sessionStorage.getItem('requestUrl')+'/axelacrm/customer-populate/item',
								JSON.stringify({ brochureItemId :'1'}),
							{ headers, withCredentials: true }).subscribe(data =>{
																this.item = data['populateItem'];
																});
	  }
	smartClickListener(event){
		  this.list.listParam['requestType']='filter';
		  this.list.listParam['smartParam']=event;
		 this.list.getData();
	 }
 
    
}
	
export class Brochure { 
brochureId: string;
brochureBranchId: string = "0";
brochureModelId: string = "0";
brochureItemId: string = "0";
}


